const CircleWrapper = styled.div`
	display: inline-block;
	box-sizing: border-box;
	width: 40px;
	height: 40px;
	padding-top: 4px;
	border-radius: 50%;
	background-color: black;
	color: white;
	text-align: center;
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif; 
	font-weight: 100;
`



export default function Post({
	data: {
		allMarkdownRemark: {edges},
	}, pageContext
}) {
	
	console.log(pageContext.currentPage, pageContext.numPages)
	const Posts = edges
	.filter(edge => !!edge.node.frontmatter.date)
	.map(edge => <PostBlock
					 key={edge.node.id}
					 title={edge.node.frontmatter.title}
					 date={edge.node.frontmatter.date}
					 tags={edge.node.frontmatter.tags}
					 img={edge.node.frontmatter.img}
					 summary={edge.node.frontmatter.summary}
					 path={edge.node.frontmatter.path}/>)
	
	return (
		<MainContainer>
			<Header />
			<SEO title={`Recent Posts - Page ${pageContext.currentPage}`} description={`전체 게시글 목록입니다.`}/>
			<PostBanner>
				<BlackContainer />
				<BackgroundContainer />
				<SubContainer style={{"height": "100%"}}>
					<BottomContainer>
						<TitleContainer>Posts - Page {pageContext.currentPage}</TitleContainer>
						<LittleContainer>전체 게시글 목록입니다.</LittleContainer>
					</BottomContainer>
				</SubContainer>
			</PostBanner>
			<SubContainer style={{"padding": "20px 15px"}}>
				{Posts}
			</SubContainer>
			<NavContainer>
				{pageContext.currentPage > 2 && <Link to={`/post/${pageContext.currentPage - 1}`}><FaChevronLeft /></Link>}
				{pageContext.currentPage == 2 && <Link to={`/post`}><FaChevronLeft /></Link>}
				<CircleWrapper>{pageContext.currentPage}</CircleWrapper>
				{pageContext.currentPage < pageContext.numPages && <Link to={`/post/${pageContext.currentPage + 1}`}><FaChevronRight /></Link>}
			</NavContainer>
			<Footer />
		</MainContainer>
	)
}

export const pageQuery = graphql`
  query postListQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date] }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            tags
            path
            title
            img
            summary
          }
        }
      }
    }
  }
`